@import "src/components/common/GlobalMixins/GlobalMixin";

.container {
  .newPostContainer {
    @include globalContainersWrapper;
    padding: 16px;
    .newPost {
      color: #fff;
      display: flex;
      .userThumb {
        display: inline-block;
        position: relative;
        margin-right: 15px;
        a {
          color: #007bff;
          text-decoration: none;
          background-color: transparent;
          transition: 0.4s;
          img{
            vertical-align: middle;
            border-style: none;
            width: 45px;
            height: 45px;
            border-radius: 50%;
          }
        }
      }
      .shareContentBox {
        width: 100%;
        height: 45px;
        .shareTextField {
          width: 100%;
          border: none;
          display: block;
          height: 44px;
          padding: 0 0 0 20px;
          position: relative;
          border-radius: 50px;
          overflow: hidden;
          background-color: #f1f1f1;
          box-shadow: inset 0 1px 10px 0 rgba(85, 85, 85, 0.2);
          box-sizing: border-box;
          outline: 1px transparent;

          // Отдельные стили для Field-а, который из redux-form
          line-height: 44px;
          resize: none;
        }
        .shareBtn {
          @include redBtn;
          top: 21px;
          right: 20px;
        }
      }
      .fade {
        display: none;
        overflow: hidden;
        transition: opacity .15s linear;
      }
      .show {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1050;
        display: block;
        width: 100%;
        height: 100%;
        overflow: visible;
        outline: 0;
      }
    }
  }

  .postsContainer {

    .card {
      @include globalContainersWrapper;
      padding: 30px;
    }
  }

}